import Lottie from "lottie-react";
import { useHistory, useParams } from "react-router-dom";
import useFetch from "../Utilities/useFetch";
import loading from "../Utilities/Loading.json";

const BlogDetails = () => {
    const { id } = useParams();
    const { data: blog, isLoading, error } = useFetch("http://localhost:8000/data/" + id);
    const history = useHistory();    
    
    const handleDelete = () => {
        fetch("http://localhost:8000/data/" + blog.id, {
            method: "DELETE"
        }).then(() => {
            history.push("/")
        })
    };

    return (
        <div className="blog-details">
            {isLoading && <Lottie className="loading" animationData={loading} />}
            {error && <div className="error-message">{error}</div>} 
            {blog && (
                <article>
                    <h1>{blog.title}</h1>
                    <h4>Written by {blog.authour}</h4>
                    <p>{blog.body}</p>
                </article>
            )}   
            <button onClick={handleDelete}>Delete Blog</button>         
        </div>
    );
};

export default BlogDetails;