import Lottie from "lottie-react";
import BlogLists from "./Components/BlogLists";
import loading from "./Utilities/Loading.json";
import useFetch from "./Utilities/useFetch";

const Home = () => {
    const { data: blogs, isLoading, error } = useFetch("http://localhost:8000/data");

    return (
        <div className="home">
            {error && <div className="error-message">{error}</div>}
            {isLoading && <Lottie className="loading" animationData={loading} />}
            {blogs && <BlogLists blogs={blogs} header="All Blogs"/>}
        </div>
    );
}

export default Home; 