import { Link } from "react-router-dom";
import Lottie from "lottie-react";
import homeIcon from "../Utilities/Home.json"

function navScrollEffect() {
    let nav = document.querySelector(".navbar");
    nav.classList.toggle("active", window.scrollY > 0);
};

window.addEventListener("scroll", navScrollEffect);

const Navbar = () => {
    const headerTitle = "Z.O.D's blog"
    return (
        <nav className="navbar">
            <h1>{headerTitle}</h1>
            <div className="links">
                <Link to="/" className="home link"><Lottie animationData={homeIcon}/></Link>
                <Link to="/createBlog" className="new-blog-link link">New Blog</Link>
            </div>
        </nav>
    );
};

export default Navbar;