import { useState } from "react";
import { useHistory } from "react-router-dom/cjs/react-router-dom";
import Lottie from "lottie-react";
import loading from "../Utilities/Loading.json";

const Create = () => {
    const [title, setTitle] = useState("");
    const [body, setBody] = useState("");
    const [authour, setAuthour] = useState("");
    const [type, setType] = useState("One Off");
    const [isLoading, setIsLoading] = useState(false);
    const history = useHistory();

    const handleSubmit = e => {
        e.preventDefault();
        const blog = { title, body, authour, type };

        setIsLoading(true);

        setTimeout(() => {
            if (isLoading === false) {
                fetch("http://localhost:8000/data/", {
                    method: "POST",
                    headers: { "Content-Type": "application/json" },
                    body: JSON.stringify(blog)
                }).then(() => {
                    console.log("New blog Added");
                    setIsLoading(false);
                    setAuthour("");
                    setBody("");
                    setTitle("");
                    setType("One Off");
                    history.push("/");
                });
            } else {
                return console.log("Already posting something");
            }
        }, 2000);
    };

    return (
        <div className="create">
            <h2>Add a New BLOG</h2>
            <form action="submit" method="post" onSubmit={handleSubmit}>
                <label>Blog Title :</label>
                <input type="text" required value={title} onChange={e => setTitle(e.target.value)} />
                <label>Blog Body :</label>
                <textarea cols="50" rows="10" required value={body} onChange={e => setBody(e.target.value)}></textarea>
                <label>Blog Author :</label>
                <input type="text" required value={authour} onChange={e => setAuthour(e.target.value)} />
                <label>Type of Book :</label>
                <select value={type} onChange={e => setType(e.target.value)}>
                    <option value="One Off">One Off</option>
                    <option value="Series">Series</option>
                </select>
                {!isLoading && <button id="add-blog">Add Blog</button>}
                {isLoading && (<button id="blog-loading-btn">Adding Blog....<Lottie className="loading-icon" animationData={loading} /></button>)}
            </form>
        </div>
    );
}

export default Create;